import { createGlobalStyle } from 'styled-components'
// eslint-disable-next-line import/no-unresolved
import { PancakeTheme } from '@pancakeswap-libs/uikit'

import ProjectFontWoff from '../fonts/CelticHand.woff'
import ProjectFontWoff2 from '../fonts/CelticHand.woff2'
import ProjectFontTtf from '../fonts/CelticHand.ttf'

declare module 'styled-components' {
  /* eslint-disable @typescript-eslint/no-empty-interface */
  export interface DefaultTheme extends PancakeTheme {}
}

const GlobalStyle = createGlobalStyle`
  @font-face {
    font-family: 'ProjectFont';
    font-style: normal;
    font-weight: 400;
    src:
        url(${ProjectFontTtf}) format('truetype'),
        url(${ProjectFontWoff2}) format('woff2'),
        url(${ProjectFontWoff}) format('woff');
  }
  * {
    font-family: sans-serif, 'ProjectFont';
  }
  body {
    background-color: ${({ theme }) => theme.colors.background};

    img {
      height: auto;
      max-width: 100%;
    }
  }
`

export default GlobalStyle
