export const fetchUserReferrer = async (account: string) => {
  const masterChefAdress = getMasterChefAddress()

  const calls = [{
    address: masterChefAdress,
    name: 'referrals',
    params: [account],
  }]

  const raw = await multicall(masterchefABI, calls)
  
  return raw[0][0]
}

export const fetchAllReferrals = async () => {
  const masterChefAdress = getMasterChefAddress()

  const callCount = [{
    address: masterChefAdress,
    name: 'referralsCount',
    params: [],
  }]

  const count = (await multicall(masterchefABI, callCount))[0][0].toNumber()

  let callsReferrersOne = []
  const callReferrersAll = []
  for (let i = 0; i < count; i++) {
    callsReferrersOne.push({
      address: masterChefAdress,
      name: 'allReferrals',
      params: [i],
    })

    if (i === count - 1 || callsReferrersOne.length % 10 === 0) {
      callReferrersAll.push(callsReferrersOne)
      callsReferrersOne = []
    }
  }

  const promisesReferrers = callReferrersAll.map(async calls => {
    const referrers = await multicall(masterchefABI, calls)
    return referrers
  })
  const allReferrers = await Promise.all(promisesReferrers)


  let callsReferralsOne = []
  const callReferralsAll = []

  for (let i = 0; i < allReferrers.length; i++) {
    for (let j = 0; j < allReferrers[i].length; j++) {
      callsReferralsOne.push({
        address: masterChefAdress,
        name: 'referrals',
        params: [allReferrers[i][j][0]],
      })
    }

    callReferralsAll.push(callsReferralsOne)
    callsReferralsOne = []
  }

  const promisesReferrals = callReferralsAll.map(async calls => {
    const referrals = await multicall(masterchefABI, calls)
    return [referrals, calls.map((obj) => obj.params)]
  })
  const allReferrals = await Promise.all(promisesReferrals)

  const result: RefRef[] = []
  for (let i = 0; i < allReferrals.length; i++) {
    for (let j = 0; j < allReferrals[i][0].length; j++) {
      result.push({
        referrer: formatAddress(allReferrals[i][0][j][0]),
        referral: formatAddress(allReferrals[i][1][j][0]),
      })
    }
  }
  
  return result
}
